.onpr_blog_home {
    position: relative;
    height: 100vh;
    width: 100vw;
    background-color: var(--bgColor);
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    overflow: auto;
    overflow-x: hidden;
}

.empty_list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.empty_list img{
    height: 5rem;
    width: 5rem;
}

.onpr_blog_home_body {
    width: 100%;
    display: flex;
    flex-direction: column;
    /* row-gap: 1rem; */
}

.articles-displayer, .more-articles-displayer {
    padding: 1rem;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(17rem, 1fr));
    grid-auto-flow: row;
    grid-auto-rows: auto;
    column-gap: 1rem;
    row-gap: .3rem;
}

.more-articles-displayer{
    padding: 0rem;
    display: flex;
    flex-direction: column;
    /* grid-auto-rows: 12rem; */
    /* grid-template-columns: repeat(auto-fill, minmax(1fr, 1fr));
    grid-auto-rows:min-content; */
    grid-gap: 2rem;
}

.articles_displayer_shimmer {
    overflow: hidden;
}

/* .articles-displayer div:nth-child(1) {
    grid-column: 1/3;
    grid-row: 1/3;
} */

/* .articles-displayer div:nth-child(5) {
    grid-column: 3;
    grid-row: 2/4;
} */

.categories_displayer {
    width: 100%;
    padding: 2rem 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: .5rem;
    background-color: var(--bgColor);
}

.categories_displayer_list_category {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 1rem;
    flex-wrap: wrap;
}

.categories_displayer_category {
    padding: .3rem 1rem;
    font-weight: 600;
    color: white;
    background-color: black;
    /* border-radius: 1rem; */
    cursor: pointer;
    white-space: nowrap;
}

#categories_displayer_category_selected {
    background-color: var(--green);
}

.categories_displayer_category:hover {
    background-color: var(--yellowWhite);
    color: var(--green);
}

#categories_displayer_category_selected:hover{
    background-color: var(--green);
    color: var(--card);
    cursor: inherit;
}

.categories_displayer_search {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: .5rem;
    border: none;
    text-align: center;
}

.categories_displayer_search_form {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    column-gap: .5rem;
}

.categories_displayer_search_form input {
    width: 40%;
    border-radius: 1.2rem;
}

.categories_displayer_search_form button {
    width: auto;
    border-radius: 1.2rem;
    background-color: var(--green);
    color: white;
    font-weight: 800;
    padding: 0rem 1rem;
    cursor: pointer;
}

.ct_displayer {
    width: 100%;
    padding: 3rem 20%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.ct_displayer div {
    width: 100%;
    height: 2rem;
}

.ct_displayer_category {
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
}


#button_in_loading {
    background-color: var(--subtitleColor);
}



@media screen and (max-width : 700px) {
    .articles-displayer div:nth-child(1) {
        grid-column: 1;
        grid-row: 1;
    }

    .articles-displayer div:nth-child(5) {
        grid-column: 1;
        grid-row: 1;
    }

    ::-webkit-scrollbar {
        width: 0rem;
      }
}

@media screen and (max-width : 950px) {
    .articles-displayer div:nth-child(5) {
        grid-column: inherit;
        grid-row: inherit;
    }
}