.comments_articles{
    width: 100%;
    background-color: var(--bgColor2);
}

.comments_articles_input{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    column-gap: .5rem;
    /* padding: .5rem; */
}

.comments_articles_input_button{
    background-color: var(--green);
    height: 100%;
    height: 2.4rem;
    padding: 10px;
    margin: 4px 0px;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    width: fit-content;
}

.comments_articles_input_button i{
    color: white;
}

.separator{
    width: 100%;
    height: .01rem;
    background-color: var(--dividerColor);
}

.comments_articles h4{
    color: var(--subtitleColor);
    margin-bottom: .3rem;
}

.comments_articles_comments_list{
    background-color: var(--bgColor2);
    border-radius: .4rem;
    padding: .5rem;
    display: flex;
    flex-direction: column;
    row-gap: .6rem;
}

.comments_articles_comment{
    /* border-bottom: .1rem solid var(--dividerColor); */
    padding: .2rem 0rem;
    display: flex;
    flex-direction: column;
    row-gap: .4rem;
}

.comments_articles_comment p:nth-child(1){
    font-weight: 900;
}

.comments_articles_comment p span{
    color: var(--secPrimaryColor);
    font-size: larger;
}

.comments_articles_comment p{
    font-size: smaller;
}

.comments_articles_comment_header{
    display: flex;
    column-gap: .5rem;
    align-items: center;
}


.comments_articles_comment_header img{
    height: 2rem;
    min-height: 2rem;
    width: 2rem;
    min-width: 2rem;
    border-radius: 50%;
    background-color: var(--bgColor);
    border: .1rem solid var(--secPrimaryColor);
    display: none;
}

.comments_articles_comment_header p{
    font-weight: 900;
}


#comments_articles_comment_body{
    font-weight: 400;
    font-size: .8rem;
}