.article-details_diplayer{
    width: 100%;
    display: grid;
    grid-template-columns: 7fr 3fr;
    gap: 0rem;
    padding: 1rem;
}

.art-overview{
    color: var(--subtitleColor);
    font-size: medium;
    font-style: italic;
}

.article_details_part, .article_details_part_shimmer{
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    padding: 0rem 1rem;
    background-color: var(--bgColor2);
    border-radius: .4rem;
}

.article_details_part_top{
    padding: 0rem;
    background-color: var(--cardColor);
    padding: .5rem;
}

.article_details_part_shimmer{
    background-color: var(--cardColor);
}


.article_details_part img{
    height: clamp(15rem, 5.657rem + 30vw, 35rem);
    min-height: clamp(15rem, 5.657rem + 30vw, 35rem);;
    width: 100%;
    overflow: hidden;
    object-fit: cover;
    /* border-radius: .4rem; */
}


.article_details_other{
    width: 100%;
}

.onpr_blog_home_shimmer{
    gap: .5rem;
    padding: .5rem;
    height: 100%;
}

.more_text{
    font-weight: 700;
}

.red_separator{
    width: 35%;
    height: .2rem;
    background-color: var(--secPrimaryColor);
    border-radius: .2rem;
}

@media screen and (max-width : 920px) {
    .article-details_diplayer{
        display: flex;
        flex-direction: column;
        width: 100%;
        row-gap: 2rem;
    }
    .article_details_part, .article_details_part_shimmer{
        padding: 0rem;
    }
}


