footer{
    display: flex;
    flex-direction: column;
    height: auto;
    margin-top: 2rem;
}

.footer-header {
    background-color: var(--card);
    width: 100%;
    height: 100%;
    padding: 5rem 2.5rem;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
    grid-auto-flow: row;
    grid-auto-rows: auto;
    grid-gap: 2rem;
    align-items: flex-start;
}
.footer-bottom{
    padding: 2rem 1.5rem;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    justify-content: center;
    align-items: center;
    background-color: var(--card);
    border-top: .1rem solid var(--greenWhite);
}

.footer-bottom p{
    text-align: center;
    padding: 0rem 3rem;
}

.foo-block {
    display: flex;
    flex-direction: column;
    row-gap: .5rem;
}

.foo-block a {
    cursor: pointer;
    width: auto;
}

.foo-block a:hover {
    font-weight: 600;
    color: var(--title);
}

.social-media {
    display: flex;
    column-gap: 1rem;
}

.social-media>a {
    cursor: pointer;
}

.social-media>a>i {
    font-size: x-large;
    cursor: pointer;
    color: var(--textColor);
}

.form-row-s-button {
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
}