.logo {
    width: 6rem;
    min-width: 6rem;
    max-width: 6rem;
    height: 6rem;
    max-height: 6rem;
    min-height: 6rem;
    object-fit: contain;
    /* background-color: red; */
}

.logo-named {
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
    width: auto;
    align-items: center;
    white-space: nowrap;
}

.logo-named img {
    height: 3rem;
    max-height: 3rem;
}

.logoName {
    color: var(--green);
}

.navbar {
    width: 100%;
    /* position: sticky; */
    top: 0;
    z-index: 100;
    background-color: var(--card);
    padding: 1rem 3rem;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    column-gap: 1.5rem;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}

.navbar-actions {
    column-gap: 1.5rem;
    flex-wrap: wrap;
    row-gap: 1rem;
    justify-content: flex-end;
}

.navbar-actions a,
.close {
    cursor: pointer;
    width: auto;
}

.navbar-actions a:hover,
.close:hover {
    color: var(--title);
    font-weight: 600;
}

.navbar-contact {
    width: 100%;
    justify-content: center;
    align-items: center;
}

.side-menu {
    position: fixed;
    display: flex;
    flex-direction: column;
    z-index: 200;
    background-color: var(--card);
    right: 0;
    width: 30rem;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px, rgba(0, 0, 0, 0.05) 0px 5px 10px;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.3s ease;
    height: 100vh;
    min-height: 100vh;
}

.side-menu .navbar {
    top: 5rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    row-gap: 3rem;
    /* padding: 4rem 2rem; */
    box-shadow: none;
}

#nav-header {
    padding: 2rem;
    display: flex;
    justify-content: flex-end;
}

.side-menu #nav-icon {
    display: none;
}

.side-menu .navbar .navbar-actions {
    display: flex;
    flex-direction: column;
    row-gap: 1.5rem;

}

.side-menu .navbar .logo-named {
    display: none;
}

.navbar-actions a,
.close {
    cursor: pointer;
    width: auto;
}

.navbar-actions a:hover,
.close:hover {
    color: var(--title);
    font-weight: 600;
}

.navbar-contact {
    width: 100%;
    justify-content: center;
    align-items: center;
}

#nav-icon {
    display: none;
}

.action-icon {
    font-size: larger;
    color: var(--green);
    cursor: pointer;
}


@media screen and (max-width : 1250px) {
    .navbar-actions {
        display: none;
    }

    #nav-icon {
        display: block;
    }

    .side-menu .navbar .navbar-actions {
        display: flex;
        flex-direction: column;
        row-gap: 1.5rem;
        justify-content: flex-start;
        align-items: flex-start;
    }
    .logo-named img {
        height: 1.5rem;
        max-height: 1.5rem;
    }
    .navbar {
        padding: .5rem 1.5rem;
    }
}

@media screen and (max-width : 920px) {

    .duo,
    #contact-us {
        display: flex;
        flex-direction: column;
    }

    .duo-left,
    .duo-right,
    .presentor-media {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 2rem;
    }

    .contact-form {
        padding: 2rem 1.5rem;
    }

    .navbar {
        padding: 1rem 1.5rem;
    }
}

@media screen and (max-width: 700px) {
    .side-menu {
        width: 20rem;
    }
}
