.article_card{
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 100%;
    width: 100%;
}

.article_card_two{
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.article_card_img{
    width: 100%;
    height: 12rem;
    min-height: 12rem;
    max-height: 12rem;
    object-fit: cover;
    background-color: var(--bgColor2);
    overflow: hidden;
}

.article_card_img_two{
    width: 40%;
    min-width: 40%;
    max-width: 40%;
    height: 7rem;
    object-fit: cover;
}

.article_card_info {
    padding: 1rem 0rem;
    overflow: hidden;
    height: fit-content;
    min-height: fit-content;
    display: flex;
    flex-direction: column;
    row-gap: .7rem;
}

.article_card_info_two{
    padding: 0rem;
    row-gap: .5rem;
}

.timarticle_card_info_time_view, .timarticle_card_info_time_view_eye{
    display: flex;
    column-gap: .7rem;
    align-items: flex-start;
}

.timarticle_card_info_time_view p{
    font-size: small;
    color: var(--text);
}

.timarticle_card_info_time_view i{
    font-size: smaller;
}

.timarticle_card_info_time_view_eye{
    column-gap: .2rem;
}

.article_card_info_topic{
    font-weight: 800;
    color: var(--green);
}

.article_card_info_overview{
    white-space: pre;
    text-overflow: ellipsis;
    overflow: hidden;
}